@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body{font-family:-apple-system, BlinkMacSystemFont, 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code,pre{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.gap{width:5px}.scale-on-hover{transition:ease 0.4s transform}.scale-on-hover:hover{transform:scale(2)}.counter-box{display:grid;grid-auto-flow:column;grid-template-columns:repeat(3, 40px);grid-template-rows:40px}.counter-box .counter{display:flex;flex-direction:row;justify-content:center;align-items:center;color:#333;text-align:center}button,.button{padding:.25rem;cursor:pointer;background:#666;border:1px ridge #333;color:#ccc;transition:0.4s ease all;opacity:0.8;text-align:center}button:disabled,.button:disabled{cursor:not-allowed;opacity:0.6}button:not([disabled]):focus,button:not([disabled]):hover,.button:not([disabled]):focus,.button:not([disabled]):hover{opacity:1}table{width:100%;border-collapse:collapse}table td,table th{border:1px solid #333;padding:.25rem}table th,table tfoot td{background:#333;color:#ccc}.align-center{text-align:center}.todo-row>td{background-color:#ccc;transition:ease 0.4s all}.todo-row.completed>td{background-color:#8f8}.toolbar{display:grid;grid-auto-flow:column;grid-template-columns:minmax(100px, auto) auto;grid-template-rows:40px;color:#ccc;box-sizing:border-box}.toolbar+.toolbar{margin-left:5px}.toolbar .toolbar-item-container{overflow:hidden}.toolbar .toolbar-item-container:not(.collapsed){max-width:40vw;transition:max-width 0.6s ease-out 0.15s}.toolbar .toolbar-item-container.collapsed{max-width:1px;transition:max-width 0.6s ease-out}.toolbar.center{justify-content:center}.toolbar-item-container{display:grid;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;grid-auto-flow:column;grid-template-columns:repeat(auto-fit, 40px);grid-template-rows:40px}.toolbar-item-container .toolbar-element{padding:0rem;width:40px}.toolbar-item-container .toolbar-element.icon{font-size:1.5rem}.toolbar-item-container .toolbar-element a{color:#ccc;text-decoration:none}.toolbar-item-container.center{justify-content:center}.label{display:inline-flex;justify-content:center;align-items:center;padding:.25rem;border:1px solid #666;box-sizing:border-box}.pointer{cursor:pointer}.form-group{display:grid;grid-auto-flow:column;grid-template-columns:minmax(100px, auto) repeat(auto-fit auto);grid-gap:5px;gap:5px}.form-group .label,.form-group .input{padding:.5rem}.form-group .label+.input,.form-group .input+button{margin-left:1rem}.input-file{display:none}.flex{display:flex}.flex.wrap{flex-wrap:wrap}.flex.no-wrap{flex-wrap:nowrap}.flex.row{flex-direction:row}.flex.column{flex-direction:column}.flex.between{justify-content:space-between}.flex.start{justify-content:flex-start}.flex.center{justify-content:center}.panel{background:#333;color:#ccc;padding:.5rem;border:1px solid #ccc;border-collapse:collapse;box-sizing:border-box}.dialog--overlay{z-index:10;position:fixed;top:0;left:0;right:0;bottom:0;display:flex;justify-content:center;align-items:center;background-color:rgba(51,51,51,0.6)}.dialog{text-align:center}

